<template>
    <div>
        <base-dialog :show="!!error" title="Something went wrong" @close="handleError">
            <p>{{ error }}</p>
        </base-dialog>
        <div v-if="isLoading">
            <base-spinner></base-spinner>
        </div>
        <base-card v-else-if="hasRequests && !isLoading">
            <h1>Requests</h1>
            <request-item v-for="request in requests" :key="request.id" :coachId="request.coachId" :email="request.email"
                :message="request.message" :to="request.to">{{
                    request }}</request-item>
        </base-card>
        <base-card v-else>
            <h2>There are not pending requests...</h2>
            <router-link to="/coaches">Return to coaches list</router-link>
        </base-card>
    </div>
</template>
<script>
import { mapGetters } from 'vuex';
import RequestItem from '../../components/requests/RequestItem.vue';
export default {
    components: {
        RequestItem
    },
    data()
    {
        return {
            isLoading: false,
            error: null
        }
    },
    computed: {
        ...mapGetters(['hasRequests', 'requests'])
    },
    created()
    {
        this.loadRequests();
    },
    methods: {
        async loadRequests()
        {
            this.isLoading = true;
            try {
                await this.$store.dispatch('fetchRequests');
            } catch (error) {
                this.error = error.message || "Something went wrong! Try again later";
            }
            this.isLoading = false;
        },
        handleError()
        {
            this.error = null;
        },
    },
}
</script>
<style scoped>
h1 {
    display: flex;
    justify-content: center;
    align-items: center;
}
</style>