<template>
    <ul>
        <li>From : <a :href="emailLink">{{ email }}</a>
            <div>
                <p>To: <strong>{{ to }} </strong></p>
                <p>Message: {{ message }}</p>
            </div>
        </li>
    </ul>
</template>
<script>
import { mapGetters } from 'vuex';


export default {
    props: ['email', 'message', 'coachId', 'to'],
    computed: {
        emailLink()
        {
            return 'mailto:' + this.email;
        },
        coachDetails()
        {
            return this.coaches.find(coach => coach.id === this.userId);

        },
        ...mapGetters(['coaches', 'userId'])
    }
}
</script>
<style scoped>
ul {
    margin-left: -40px;
}

li {
    margin: 1rem 0;
    border: 1px solid #ccc;
    padding: 1rem;
    border-radius: 10px;
    list-style: none;
}

a {
    color: #3d008d;
    text-decoration: none;
    font-weight: bold;
    cursor: pointer;
}

a:hover,
a:active {
    color: #8d007a;
}

p {
    margin: 0.5rem 0 0 0;
}
</style>